@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700);
@import url(https://fonts.googleapis.com/css?family=Anton);
* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  font-family: 'Lato', 'Helvetica Neue', sans-serif;
  background-color: #F4F4F7;
}

h1 {
  font-family: 'Anton', sans-serif;
}

a.link-unstyled {
  color: inherit;
}

a.link-unstyled:hover {
  color: inherit;
  text-decoration: none;
}

.btn {
  padding: 0.375rem 2rem;
}

.btn.btn-primary {
  color: #fff;
  background-color: #7DCD40;
  border-color: #7DCD40;
}

.btn.btn-primary:not(:disabled):not(.disabled):hover {
  color: #fff;
  background-color: #7DCD40;
  border-color: #7DCD40;
}

.btn.btn-primary:not(:disabled):not(.disabled):active {
  color: #fff;
  background-color: #7DCD40;
  border-color: #7DCD40;
}

.btn.btn-primary:not(:disabled):not(.disabled):focus {
  box-shadow: 0 0 0 0.2rem rgba(125, 205, 64, 0.5);
}

.btn.btn-danger {
  color: #fff;
  background-color: #CD4040;
  border-color: #CD4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):hover {
  color: #fff;
  background-color: #CD4040;
  border-color: #CD4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):active {
  color: #fff;
  background-color: #CD4040;
  border-color: #CD4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):focus {
  box-shadow: 0 0 0 0.2rem rgba(255, 64, 64, 0.5);
}
.Navbar {
  width: 100%;
  padding: 0.5rem 0;
  background-color: #272829;
  position: fixed;
  z-index: 3;
}

.Navbar__brand {
  color: #ffffff;

  display: flex;
  align-items: center;
}

.Navbar__brand:hover {
  color: #ffffff;
  text-decoration: none;
}

.Navbar__brand-logo {
  margin-right: 0.5rem;
  width: 50px;
}
.Home {
  width: 100%;
  min-height: calc(100vh - 60px);
  background: url(/starwarsapi/static/media/stars.d4d2376b.svg);
  background-repeat: repeat;

  color: #ffffff;

  display: flex;
  align-items: center;
}

.btn-warning {
  background: #FFC500;
  font-weight: bold;
}

.Home__col {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.Home__col {
  display: flex;
  justify-content: center;
  align-self: center;
}

.Home__col img {
  width: 50%;
}

@media screen and (min-width: 768px) {
  .Home__col {
    align-items: center;
  }
}
body {
  background: url(/starwarsapi/static/media/stars.d4d2376b.svg), #000000;
  color: #fff;
}

.Badges__hero {
  width: 100%;
  padding: 2rem 0;
  background: url(/starwarsapi/static/media/stars.d4d2376b.svg), #1B1B25;
  background-repeat: repeat;
  margin-bottom: 1rem;
}

.Badges__hero>.Badges__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Badges__container {
  width: 100%;
  max-width: 580px;
  margin: 0 auto;
  padding: 0 1rem;
}

.Badges_conf-logo {
  margin-bottom: 2rem;
}

.Badges__buttons {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 1rem;
}

.BadgesList ul>li {
  margin-bottom: 1rem;
  border-radius: 10px;
  border: none; 
  box-shadow: 0px 0px 7px 3px #ffe919;
  background: #000000;
  
}

.BadgesList ul {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-content: center;
  align-items: center;
  grid-gap: 25px;
  gap: 25px;
}

@media screen and (max-width: 950px) {
  .BadgesList ul {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (max-width: 620px) {
  .BadgesList ul {
    grid-template-columns: repeat(1, 1fr);
  }
  .Badges__container {
    max-width: 300px;
}
}
@media screen and (max-width: 475px) {
  .BadgesList ul {
    grid-template-columns: repeat(1, 1fr);
  }
  .Badges__container {
    max-width: 300px;
}
}

.BadgesListItem {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  width: 280px;
  height: 500px;
  border-radius: 5px;
  padding: 5px;
}

.BadgesListItem__avatar {
  border-radius: 10px;
  background: #a3a3a3;
  width: 100%;
  height: auto;
}

.Badges-info {
  display: flex;
}

.font-weight-bold {
  font-weight: bold;
  margin-right: 8px;
}

.Info-line {
  line-height: 3px;
}
/* Animation properties */
.star {
    -webkit-animation: star 2s ease-out infinite;
            animation: star 2s ease-out infinite;
  }
  .wars {
    -webkit-animation: wars 10s ease-out infinite;
            animation: wars 10s ease-out infinite;
  }
  .byline span {
    -webkit-animation: spin-letters 10s linear infinite;
            animation: spin-letters 10s linear infinite;
  }
  .byline {
    -webkit-animation: move-byline 10s linear infinite;
            animation: move-byline 10s linear infinite;
  }

  .star {
      font-size: 2.2rem;
      color: #FFE300;
  }
  
  /* Keyframes */
  @-webkit-keyframes star {
    0% {
      opacity: 0;
      -webkit-transform: scale(1.5) translateY(-0.75em);
              transform: scale(1.5) translateY(-0.75em);
    }
    20% {
      opacity: 1;
    }
    89% {
      opacity: 1;
      -webkit-transform: scale(1);
              transform: scale(1);
    }
    100% {
      opacity: 0;
      -webkit-transform: translateZ(-50em);
              transform: translateZ(-50em);
    }
  }
  @keyframes star {
    0% {
      opacity: 0;
      -webkit-transform: scale(1.5) translateY(-0.75em);
              transform: scale(1.5) translateY(-0.75em);
    }
    20% {
      opacity: 1;
    }
    89% {
      opacity: 1;
      -webkit-transform: scale(1);
              transform: scale(1);
    }
    100% {
      opacity: 0;
      -webkit-transform: translateZ(-50em);
              transform: translateZ(-50em);
    }
  }
  
  @-webkit-keyframes spin-letters {
    0%, 10% {
      opacity: 0;
      -webkit-transform: rotateY(90deg);
              transform: rotateY(90deg);
    }
    30% {
      opacity: 1;
    }
    70%, 86% {
      -webkit-transform: rotateY(0);
              transform: rotateY(0);
      opacity: 1;
    }
    95%, 100% {
      opacity: 0;
    }
  }
  
  @keyframes spin-letters {
    0%, 10% {
      opacity: 0;
      -webkit-transform: rotateY(90deg);
              transform: rotateY(90deg);
    }
    30% {
      opacity: 1;
    }
    70%, 86% {
      -webkit-transform: rotateY(0);
              transform: rotateY(0);
      opacity: 1;
    }
    95%, 100% {
      opacity: 0;
    }
  }
  
  @-webkit-keyframes move-byline {
    0% {
      -webkit-transform: translateZ(5em);
              transform: translateZ(5em);
    }
    100% {
      -webkit-transform: translateZ(0);
              transform: translateZ(0);
    }
  }
  
  @keyframes move-byline {
    0% {
      -webkit-transform: translateZ(5em);
              transform: translateZ(5em);
    }
    100% {
      -webkit-transform: translateZ(0);
              transform: translateZ(0);
    }
  }
  
  /* Make the 3D work on the container */
  .starwars-demo {
    -webkit-perspective: 800px;
            perspective: 800px;
    -webkit-transform-style: preserve3d;
            transform-style: preserve3d;
  }
  
  /* General styles and layout */
  
  .starwars-demo {
    height: 17em;
    left: 60%;
    position: absolute;
    top: 70%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 34em;
  }
  
  .byline span {
    display: inline-block;
  }
  
  img {
    width: 100%;
  }
  
  .star, .byline {
    position: absolute;
  }
  
  .star {
    top: -0.75em;
  }
  
  .byline {
    color: #fff;
    font-family: "ITC Serif Gothic", Lato;
    font-size: 2.25em;
    left: -2em;
    letter-spacing: 0.4em;
    right: -2em;
    text-align: center;
    text-transform: uppercase;
    top: 29%;
  }
  
  /*** Media queries for adjusting to different screen sizes ***/
  
  @media only screen and (max-width: 600px) {
    .starwars-demo {
      font-size: 10px;
    }
  }
  
  @media only screen and (max-width: 480px) {
    .starwars-demo {
      font-size: 7px;
    }
  }
  
  
